	module.exports = {"pathContext":{"docHtml":"<h1>json-stream <a href=\"http://travis-ci.org/mmalecki/json-stream\"><img src=\"https://secure.travis-ci.org/mmalecki/json-stream.png\" alt=\"Build Status\"></a></h1>\n<p>New line-delimeted JSON parser with a stream interface.</p>\n<h2>Installation</h2>\n<pre><code>npm install json-stream\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var JSONStream = require('json-stream');\n\nvar stream = JSONStream();\n\nstream.on('data', function (chunk) {\n  console.dir(chunk);\n});\nstream.write('{\"a\":');\nstream.write('42}\\n');\nstream.write('{\"hel');\nstream.write('lo\": \"world\"}\\n');\n</code></pre>\n<p>Will output:</p>\n<pre><code>{ a: 42 }\n{ hello: 'world' }\n</code></pre>\n<p>If invalid JSON gets written, it's silently ignored.</p>","docPath":"argo-ci/node_modules/json-stream/readme","proj":"argo-ci"}}

